.movie {
    border: 1px solid #e0e0e0; /* 테두리 */
    border-radius: 8px; /* 모서리 둥글게 */
    overflow: hidden; /* 내용이 넘칠 경우 숨김 */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
    margin-bottom: 20px; /* 아래쪽 여백 */
    background: white; /* 배경색 */
  }
  
  .movie img {
    width: 100%; /* 너비 */
    height: auto; /* 높이 자동 조절 */
    display: block; /* 블록 요소로 표시 */
  }
  
  .movie h2 {
    margin: 0; /* 여백 없음 */
    padding: 10px; /* 내부 여백 */
    background-color: #f8f8f8; /* 제목 배경색 */
    color: #333; /* 제목 글자색 */
    font-size: 1.5em; /* 글자 크기 */
  }
  
  .movie p {
    padding: 0 10px; /* 내부 여백 */
    color: #666; /* 글자색 */
    font-size: 0.9em; /* 글자 크기 */
    line-height: 1.6; /* 줄 간격 */
  }
  
  .movie ul {
    list-style: none; /* 목록 스타일 없음 */
    padding: 10px; /* 내부 여백 */
    margin: 0; /* 여백 없음 */
    display: flex; /* 플렉스박스로 표시 */
    flex-wrap: wrap; /* 내용이 넘칠 경우 다음 줄로 */
    gap: 5px; /* 요소 사이 간격 */
  }
  
  .movie li {
    background-color: #eeeeee; /* 배경색 */
    padding: 5px 10px; /* 내부 여백 */
    border-radius: 16px; /* 모서리 둥글게 */
    font-size: 0.8em; /* 글자 크기 */
  }
  