/* 전체 페이지의 기본 스타일 */
body {
    background-color: #f4f4f4;
    margin: 0;
    font-family: 'Arial', sans-serif;
  }
  
  /* 로딩 텍스트 스타일 */
  strong {
    display: block;
    text-align: center;
    margin-top: 20%;
    font-size: 2em;
    color: #333;
  }
  
  /* 영화 목록 컨테이너 스타일 */
  div {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 20px;
  }
  
  /* 각 영화 카드 스타일 */
  .movie {
    border: 1px solid #ddd;
    border-radius: 10px;
    overflow: hidden;
    width: 200px;
    background: white;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    transition: transform 0.3s ease;
  }
  
  .movie:hover {
    transform: translateY(-10px);
  }
  
  /* 영화 이미지 스타일 */
  .movie img {
    width: 100%;
    height: 300px;
    object-fit: cover;
  }
  
  /* 영화 정보 스타일 */
  .movie-info {
    padding: 15px;
  }
  
  .movie-title {
    font-size: 1.2em;
    margin-bottom: 5px;
  }
  
  .movie-summary {
    font-size: 0.9em;
    line-height: 1.4;
    color: #666;
  }
  
  .movie-genres {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
  }
  
  .movie-genres li {
    background: #eee;
    padding: 5px 10px;
    border-radius: 20px;
    font-size: 0.8em;
  }
  